nav {
  background-color: rgba(0, 0, 0, 0.3);
  width: max-content;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.6rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.3rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}

nav a.active {
  background-color: var(--color-primary);
  color: var(--color-bg);
}

nav-title p {
  font-size: 0.5rem;
  color: red;
  
}

/* ================== MEDIA QUERIES (SMALL DEVICES) ================= */
@media screen and (max-width: 400px) {
  nav {
    display: flex;
    flex-direction: row;
    gap: 0.01rem;
    border-radius: 0rem;
    width: 100vh;
    justify-content: center;
  }
}
